.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

#root .box{
      margin: 20px auto;
		width: 500px;
		min-height: 400px;
		background: #f5f5f5;
		border: 1px solid #ccc;
		border-radius: 4px;
}
#root .box .head{
	  font-size:26px ;
		text-align: center;
		margin: 30px auto 15px;
}
#root .box .list{
	width: 400px;
    		min-height: 200px;
    		background: #fff;
    		border-radius: 2px;
    		border: 1px solid #CCCCCC;
    		margin: 0 auto;
}
#root .box .list .listItem{
	height: 40px;
    		border-bottom: 1px solid #CCCCCC;
    		line-height: 40px;
    			-webkit-transition: 0.5s;
    			-o-transition: 0.5s;
    			transition: 0.5s;
}
#root .box .list .listItem input{
	margin: 0 10px 0 15px;
}
#root .box .list .listItem .txt{
	display: inline-block;
}
#root .box .list .checked{
	background: #dff1d9;
} 
#root .box .list .checked .txt{
	text-decoration: line-through;
}
.finish{
	width: 390px;
	height: 40px;
	line-height: 40px;
	padding-left: 10px;
	margin: 0 auto;
	color: gray;
}
.addBox{
	width: 400px;
	margin: 10px auto;
}
.addBox .inputTxt{
	height: 30px;
	width: 300px;
	border-radius: 2px;
	border: 1px solid #CCCCCC;
	padding-left: 20px;
}
.addBox  button{
	border:none;
	background: #327cbd;
	width: 60px;
	height: 30px;
	margin-left: 18px;
	color: #fff;
	border-radius: 4px;
	cursor: pointer;
	opacity: 0.5;
	-webkit-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
}
.addBox  button:hover{
	opacity: 1;
	-webkit-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
}



